@font-face {
  font-family: "Kanit";
  src: url(./kanit_semibold.ttf);
}

@font-face {
  font-family: "Roboto";
  src: url(./Roboto-Regular.ttf);
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url(./Roboto-Medium.ttf);
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Roboto";
  src: url(./Roboto-Bold.ttf);
  font-weight: bold;
  font-style: normal;
}

html {
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 0;
  height: 100%;
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.bg-dim {
  background: rgba(0, 0, 0, 0.2);
}

.bg-custom {
  background: #f2f4f9;
}

.logo-text {
  font-family: "Kanit", sans-serif;
}

.white-link {
  color: rgba(255, 255, 255, 0.5);
}

.white-link:hover {
  color: #fff;
  text-decoration: none;
}

.card-clickable {
  text-decoration: none;
  background: #fff;
}

.card-clickable:hover {
  text-decoration: none;
  background: rgba(200, 200, 200, 0.2);
}

.footer {
  width: 100%;
  position: absolute;
  bottom: 0;
  background: #014ec4;
  right: 0;
}

.font-xl {
  font-size: x-large !important;
}

.font-weight-medium {
  font-weight: 500;
}

.shadow-cm {
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2) !important;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2) !important;
}

/*
 * Navbar notification badge
 */

span.notification-badge {
  position: relative;
  top: -10px;
  right: 5px;
  background: red;
}
